<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" icon="el-icon-plus" @click="add()">新增</el-button>
      </div>
      <el-table :data="tableData" border class="table" id="multipleTable">
        <el-table-column label="序号">
          <template slot-scope="scope">
            <span>{{scope.$index+(cur_page-1)*cur_size+1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="职位名称" prop="name">
        </el-table-column>
        <el-table-column label="招聘人数" prop="people">
        </el-table-column>
        <el-table-column label="薪资" prop="salary">
        </el-table-column>
        <el-table-column label="地址" prop="address">
        </el-table-column>
        <el-table-column label="截止时间" prop="endtime">
        </el-table-column>
        <el-table-column label="工作要求" prop="wordrequire">
        </el-table-column>
        <el-table-column label="工作内容" prop="wordinfo">
        </el-table-column>
        <el-table-column label="操作" width="280" align="center">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-document" @click="handleDetails(scope.$index, scope.row)">详情
            </el-button>
            <el-button type="text" icon="el-icon-edit" @click="handleI(scope.$index, scope.row)">编辑</el-button>
            <el-button type="text" icon="el-icon-setting" class="red" @click="open(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog title="新增" :visible.sync="showAdd">
      <el-form :model="form" ref="form" :rules="rules">
        <el-form-item label="职位名称" prop="name" :label-width="formLabelWidth">
          <el-input v-model="form.name"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="招聘人数" prop="people" :label-width="formLabelWidth">
          <el-input v-model="form.people"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="薪资" prop="salary" :label-width="formLabelWidth">
          <el-input v-model="form.salary"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address" :label-width="formLabelWidth">
          <el-input v-model="form.address"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="截止时间" prop="endtime" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.endtime"
            type="datetime"
            placeholder="选择日期时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="工作要求" prop="wordrequire" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="form.wordrequire"  autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
        <el-form-item label="工作内容" prop="wordinfo" :label-width="formLabelWidth">
          <el-input type="textarea"  v-model="form.wordinfo" autocomplete="off" class="mz_wb"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showAdd = false">取 消</el-button>
        <el-button type="primary" @click="handleAdd('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import http from '../../../assets/js/http';

  export default {
    name: 'recruit',
    data() {
      return {
        tableData: [],
        total: 0,
        cur_page: 1,
        cur_size: 10,
        form:{},
        showAdd:false,
        rules: {
          name: [{
            required: true,
            message: '请填写职位名称',
            trigger: 'blur'
          }],
          people: [{
            required: true,
            message: '请填写招聘人数',
            trigger: 'blur'
          }],
          salary: [{
            required: true,
            message: '请填写薪资',
            trigger: 'blur'
          }],
          address: [{
            required: true,
            message: '请填写地址',
            trigger: 'blur'
          }],
          endtime:[{
            required: true,
            message: '请选择截止时间',
            trigger: 'change'
          }],
          wordrequire: [{
            required: true,
            message: '请填写工作要求',
            trigger: 'blur'
          }],
          wordinfo: [{
            required: true,
            message: '请填写工作内容',
            trigger: 'blur'
          }],
        },
        formLabelWidth: '100px'
      }
    },
    created() {
      this.getCategoryData();
    },
    activated(){
      this.getCategoryData();
    },
    computed: {},
    mounted() {

    },
    methods: {
      // 分页导航
      handleCurrentChange(val) {
        this.cur_page = val;
        this.getCategoryData();
      },
      handleSizeChange(val) {
        this.cur_size = val;
        this.getCategoryData();
      },
      getCategoryData() { //
        let that = this;
        const data = {
          "list_rows": parseInt(that.cur_size),
          "page": parseInt(that.cur_page),
        };
        that.apiGet('/admin/djzp', data).then((res) => {
          that.handelResponse(res, (data) => {
            that.total = data.data.total;
            that.tableData = data.data.data;
          })
        })
      },
      add() {
        var that= this;
        that.showAdd = true;
        that.form = {}
      },
      handleAdd(formName){
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var that = this;
            var d = new Date(that.form.endtime);
            var year = d.getFullYear(),
              month = d.getMonth()+1,
              day = d.getDate();
            var h = d.getHours();//12
            var m = d.getMinutes(); //12
            var s = d.getSeconds();
            if (h < 10) {
              h = "0" + h;
            }
            if (m < 10) {
              m = "0" + m;
            }
            if (s < 10) {
              s = "0" + s;
            }
            var params = {
              name:that.form.name,
              salary:that.form.salary,
              address:that.form.address,
              people:that.form.people,
              endtime:year+'-'+month+'-'+day+''+h+':'+m+':'+s,
              wordrequire:that.form.wordrequire,
              wordinfo:that.form.wordinfo,
              datoken:that.form.datoken
            };
            that.apiPost('/admin/djzpadd', params).then((res) => {
              this.handelResponse(res, (data) => {
                that.$message.success('成功');
                // that.$router.go(-1);
                that.getCategoryData();
                that.showAdd = false
              })
            })
          } else {
            return false;
          }
        });
      },
      //详情
      handleDetails(index, row) {
        this.$router.push({
          path: '/recruitD',
          query:{
            datoken:row.datoken
          }
        });
      },
      //编辑
      handleI(index, row) {
        this.$router.push({
          path: '/recruitC',
          query:{
            datoken:row.datoken
          }
        });
      },
      //删除
      open(index, row) {
        var that = this;
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let param = {
            datoken: row.datoken,
          };
          this.apiPost('/admin/djzpdelete', param).then((res) => {
            this.handelResponse(res, (data) => {
              this.$message.success('成功');
              this.getCategoryData()
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    mixins: [http]
  }
</script>

<style scoped>
  img {
    width: 100px;
    height: 100px;
  }
  .handle-box {
    margin-bottom: 20px;
  }
  .del-dialog-cnt {
    font-size: 16px;
    text-align: center
  }
  .table {
    width: 100%;
    font-size: 14px;
  }
  .red {
    color: #ff0000;
  }
  .handle-input {
    width: 300px;
  }
  .el-radio {
    margin-bottom: 20px;
  }
  textarea{
    height: 100px !important;
  }
</style>



